  width: 100vw;
  height: 100vh;
  background: rgba(233, 236, 241, 0.8);
  z-index: 96;
  transform: scaleX(1);
  transform-origin: left;
  transition: transform 1s ease-out;
}

.curtain.open {
  transform: scaleX(0);
}

.curtain-inner {
  width: 200%;
  height: 100%;
  background: rgba(233, 236, 241, 0.8);
  position: absolute;
  left: 0;
  top: 0;
  z-index: 96;
}

.work__main {
  width: 100%;
  max-width: 1280px;
  padding: 60px 0;
  display: flex;
  gap: 15px;
  flex-direction: column;
  justify-content: center;
}

.work__main_cont {
  flex: 1;
  display: flex;
  align-items: flex-end;
}

.work__main_cont .work__main_cont-wrapper {
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  justify-content: space-between;
  gap: 30px;
  position: relative;
  /*top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);*/
  flex-direction: row;
  width: 100%;
  text-align: left;
  align-items: center;
}

.work__main_cont .work__main_cont-wrapper h1 {
  font-family: "Black Ops One", system-ui;
  font-weight: 400;
  font-style: normal;
}
.work__main_cont img {
  width: 30%;
}

.work__main_cont h1 {
  flex: 1;
}
.work__main_text {
  position: relative;
  flex: 1;
  width: 100%;
  min-height: 150px;
  text-align: left;
}
.work__section {
  display: flex;
  flex-direction: column;
  scroll-snap-type: y mandatory;
  overflow-y: scroll;
  height: 100vh;
  gap: 25px;
  scroll-behavior: smooth;
  align-items: center;
  max-width: 100%;
  scrollbar-width: thin; /* Firefox -   */
  scrollbar-color: #4a90e2 #1c1c1c; /*     (Firefox) */
}
.work__section::-webkit-scrollbar {
  width: 8px; /*   */
}

.work__section::-webkit-scrollbar-track {
  background: #1c1c1c; /*   */
  border-radius: 10px; /*   */
}

.work__section::-webkit-scrollbar-thumb {
  background-color: #4a90e2; /*   */
  border-radius: 10px; /*   */
  border: 2px solid #1c1c1c; /*  thumb  */
}

.work__section::-webkit-scrollbar-thumb:hover {
  background-color: #3b7ddf; /* hover  thumb  */
}

.slider {
  height: 100vh;
  scroll-snap-align: start;
  scroll-snap-stop: always;
  flex: 0 0 auto;
}

.box {
  display: flex;
  justify-content: center;
  width: 100%;
  color: white;
  /*opacity: 0;
  transition:
    opacity 0.5s ease-out,
    transform 0.5s ease-out;
  transform: translateY(20px);*/
  /* scroll-snap-align: start; */
  position: relative;
  max-width: 800px;
  box-sizing: border-box;
  align-items: center;
}

.in-view {
  opacity: 1;
  transform: translateY(0);
}

.contact-header p {
  color: #e0e0e0;
  line-height: 2;
  padding-left: 3em;
}

.contact-content {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 8vh;
  margin: 10vh 3em;
}

.contact-card .contact-image {
  overflow: hidden;
  height: 60%;
  flex: 0 0 auto;
}

.contact-card .contact-image img {
  transform: translateY(-65%);
  top: 50%;
  position: relative;
}

.contact-link {
  text-decoration: none;
  color: inherit;
  background: #2c2c2c;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
  transition:
    transform 0.3s ease,
    box-shadow 0.3s ease;
  flex-direction: column;
  display: flex;
  height: 350px;
}

.contact-link:hover {
  transform: scale(1.05);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.7);
}

.contact-image img {
  width: 100%;
  height: auto;
}

.contact-info {
  padding: 20px;
  height: 40%;
  flex: 1;
}

.contact-info h3 {
  font-size: 1.5rem; /* Slightly larger font size for the title */
  color: #ffffff; /* White text color */
  margin-bottom: 10px; /* Space between title and description */
}

.contact-info p {
  font-size: 1rem; /* Normal text size for description */
  color: #b0b0b0; /* Light grey text for description */
}

@media (max-width: 1280px) {
  .about_me__section {
    margin-bottom: 0;
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
  }
}
@media (max-width: 768px) {
  .work__section {
    height: calc(100vh - 50px);
    top: 50px;
  }
  .slider {
    height: calc(100vh - 50px);
  }
  .work__main_cont {
    align-items: center;
  }
  .contact-header p {
    padding-left: 0;
  }
  .contact-info h3 {
    font-size: 1.25rem;
  }
}
</style>
