  align-items: center;
  justify-content: center;
  visibility: hidden;
}

.popup-overlay.show {
  opacity: 1;
  visibility: visible;
}

.popup-content {
  background-color: #3a455d;
  color: #dfe4ea;
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
